@import "_config.scss";

$color: #001f3f;
/* 
  ##Device = Most of the Smartphones Mobiles (Portrait)
  ##Screen = B/w 320px to 479px
*/
@media screen and (min-width: 320px) and (max-width: $maxWidthPortrait) {
    .projects-new{
        h1 {
            text-align: center;
            font-family: "Quicksand", sans-serif;
            color: $color;
            font-size: 4.083vw;
            margin-bottom: 2.389vw;
            margin-top: 1.389vw;
        }
        a{
            text-decoration: underline;
        }
        .more-projects{
            font-size: 14px;
            color: $color;
            z-index: 999;
            margin-top: 10px;
            text-align: right;
            font-weight: normal;
            font-family: "Poppins", sans-serif;
        }
        .ind-exp-container{
            display: flex;
            flex-direction: row;
            flex-wrap: wrap;
            justify-content: center;
            padding: 20px;
            overflow-y: scroll;
        }
        .wrapper{
            position: relative;
            width: 290px;
            height: 290px;
            cursor: pointer;
        }
        iframe{
            margin-top: -20px;
        }
        .collage{
            img{
                width: 60px !important;
                border-radius: 0px !important;
            }
            
            margin: auto;
            margin-top: -10px;
        }
        .exp1{
            img{
                width: 290px;
                border-radius: 20px;
            }
            .secondary-wrapper{
                position: absolute;
                top: 0;
                left: 0;
                display: flex;
                width: 290px;
                flex-direction: column;
                align-items: center;
                justify-content: center;
            }
            .caption:first-child{
                margin-bottom: 138px; 
                margin-top: 20px;
            }
            .caption{
                font-size: 25px;
                color: white;
                z-index: 999;
                text-align: center;
                font-weight: normal;
                font-family: "Poppins", sans-serif;
                text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
                background-color: #fa997c;
                padding: 3px;
                padding-left: 10px;
                padding-right: 10px;
                border-radius: 10px;
            }
            .tags{
                margin-top: 8px;
                max-width: 250px;
                ul,li{
                    list-style-type: none;
                    display: inline-block;
                    text-align: center;
                    font-family: 'Abel', sans-serif;
                    color: white;
                    z-index: 999;
                    font-size: 12px;
                    padding-right: 4px;
                    padding-left: 4px;
                    margin-right: 5px;
                    margin-bottom: 5px;
                    border-radius: 5px;
                }
                li{
                    background-color: #3C99DC;
                }
            }
            .caption-long{
                cursor: default;
                display: flex;
                flex-direction: column;
                .closee{
                    font-size: 30px;
                    color: white;
                    font-family: "Lato", sans-serif;
                    text-align: right;
                    margin: 0;
                    cursor: pointer;
                }
                .text{
                    a{
                        color: white;
                        text-decoration: underline;
                    }
                    font-size: 12px;
                    color: white;
                    z-index: 999;
                    text-align: left;
                    vertical-align: middle;
                    font-family: "Lato", sans-serif;
                    margin-top: -22px;
                    margin-left: 20px;
                    margin-right: 20px;
                    margin-bottom: 20px;
                    em{
                        font-weight: 700;
                        font-style: normal;
                    }
                }
                width: 250px;
                height: 250px;
                background-color: rgba(60, 153, 220, 0.95);
                padding: 9px;
                border-radius: 10px;
                margin-top: 20px;
            }
            .time{
                font-size: 20px;
                font-family: "Lato", sans-serif;
                color: $color;
                text-align: right;
                margin-bottom: 20px;
            }
            margin: auto;
            margin-bottom: 20px;
        }
        .exp-container{
            width: 90%;
            height: 81vh;
            background-color: #f3feff;
            border-radius: 1.042vw;
            display: flex;
            padding: 20px;
            flex-direction: column;
        }
        position: -webkit-sticky;
        position: sticky;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        color: $color;
        left: 14.583vw;
        right: 30%;
        width: 100%;
        height: 100vh;
    }
}

