@import "_config.scss";
$color: #001f3f;
/* 
  ##Device = Most of the Smartphones Mobiles (Portrait)
  ##Screen = B/w 320px to 479px
*/
@media screen and (min-width: 320px) and (max-width: $maxWidthPortrait) {
  .aboutMe {
    .skillsets {
      h4 {
        text-align: center;
        font-family: "Quicksand", sans-serif;
        color: $color;
        font-size: 4.083vw;
        margin-bottom: 1vh;
        // margin-top: 3.389vw;
      }
      .ProgLang, .Frame, .Hardware, .Course {
        h2 {
          font-size: 1.419vh;
          font-family: "Lato", sans-serif;
          text-align: center;
          margin: 10px;
        }  
        line-height: 10px;
        .large {
          max-width: none !important;
          ul,li{
            font-size: 1.6vh !important;
            margin-right: 1vh !important;
            margin-bottom: 1vh !important;
          }
        }
        .new-list{
          max-width: 100px;
          ul,li{
            list-style-type: none;
            display: inline-block;
            text-align: center;
            font-family: 'Abel', sans-serif;
            color: #001f3f;
            z-index: 999;
            font-size: 1.5vh;
            padding-right: 3px;
            padding-left: 3px;
            margin-right: 2px;
            margin-bottom: 5px;
          }
          li{
            text-align: center;
            min-width: 12px;
            background: linear-gradient(180deg,rgba(255,255,255,0) 40%, #FFD0AE 40%);
          }
          margin-right: auto;
          margin-left: auto;
        }
        display: flex;
        flex-direction: column;
      }
      .containerr{
        padding: 0;
        display: flex;
        flex-direction: row;
      }
      display: flex;
      flex-direction: column;
      margin-bottom: 30px;
      width: 90%;
      height: 25vh;
      background-color: #f3feff;
      border-radius: 1.042vw;
    }
    overflow-y: hidden;
    overflow-x: hidden;
    position: -webkit-sticky;
    position: sticky;
    color: $color;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    left: 14.583vw;
    right: 30%;
    width: 100%;
    line-height: 3.472vw;
    height: 100vh;
  }
}

/* 
  ##Device = Laptops, Desktops
  ##Screen = B/w 1025px to 1280px
*/
@media screen and (min-width: 1025px) {
  .aboutMe {
    /* A brief introduction */
    .intro {
      display: none;
      h1 {
        text-align: center;
        font-family: "Quicksand", sans-serif;
        color: $color;
        font-size: 2.083vw;
        margin-bottom: 1.389vw;
        margin-top: 1.389vw;
      }
      width: 50%;
      height: 81vh;
      background-color: #f3feff;
      border-radius: 1.042vw;
      // display: flex;
      padding: 20px;
      margin-right: 50px;
      flex-direction: column;
      overflow-y: scroll;
    }
    .skillsets {
      margin-right: 70px;
      padding: 20px;
      h4 {
        text-align: center;
        font-family: "Quicksand", sans-serif;
        color: $color;
        font-size: 2.083vw;
        margin-top: 1.389vw;
      }
      .ProgLang, .Frame, .Hardware, .Course {
        h2 {
          font-size: 1.319vw;
          font-family: "Lato", sans-serif;
          display: inline;
          margin: 0.7vw;
          margin-top: 0.7vw;
          margin-bottom: 0.85vw;
        }  
        line-height: 17em; //controls thickness of the tags
        .large {
          max-width: 20vw !important;
          ul,li{
            font-size: 1.3vw !important;
            margin-right: 0 !important;
            margin-bottom: 1vw !important;
          }
        }
        .new-list{
          max-width: 18vw;
          ul,li{
            list-style-type: none;
            display: inline-block;
            text-align: center;
            font-family: 'Abel', sans-serif;
            color: #001f3f;
            z-index: 999;
            font-size: 1vw;
            padding-right: 0.3vw;
            padding-left: 0.3vw;
            margin-right: 0.4vw;
            margin-bottom: 0.4vw;
            // border-radius: 6px;
          }
          a {
            text-decoration: none;
          }
          a:hover {
            background: linear-gradient(180deg,rgba(255,255,255,0) 5%, #FFD0AE 5%);
          }
          li{
            text-align: center;
            min-width: 30px;
            // background-color: rgb(245, 181, 53);
            background: linear-gradient(180deg,rgba(255,255,255,0) 35%, #FFD0AE 35%);
            // text-shadow: 0px 2px 2px rgba(0, 0, 0, 0.25);
          }
          margin-right: auto;
          margin-left: auto;
        }
        display: flex;
        flex-direction: column;
        align-items: center;
      }
      .containerr {
        overflow-y: scroll;
      }
      display: flex;
      flex-direction: column;
      width: 35%;
      height: 60vh;
      background-color: #f3feff;
      border-radius: 1.042vw;
      
    }
    a {
      color: $color;
      text-decoration: underline;
      text-align: center;
    }
    p {
      padding: 0;
      font-family: "Lato", sans-serif;
      line-height: 2.778vw;
      font-size: 1.181vw;
      em {
        color: $color;
        text-emphasis-style: none;
        font-style: normal;
        font-weight: bold;
      }
    }
    position: -webkit-sticky;
    position: sticky;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    color: $color;
    left: 14.583vw;
    right: 30%;
    width: 80%;
    height: 100vh;
    padding: 20px;
    line-height: 3.472vw;
  }
}

/* 
  ##Device = Tablets, Ipads (portrait)
  ##Screen = B/w 768px to 1024px
*/
