      padding: 20px;
      h4 {
        text-align: center;
        font-family: "Quicksand", sans-serif;
        color: $color;
        font-size: 2.283vh;
        margin-top: 1.389vw;
      }
      .containerr{
        padding: 0;
        display: flex;
        flex-direction: row;
        justify-content: center;
      }
      .ProgLang, .Frame, .Hardware, .Course {
        h2 {
          font-size: 1.219vw;
          font-family: "Lato", sans-serif;
