      font-size: 1.4vh;
      em {
        color: $color;
        text-emphasis-style: none;
        font-style: normal;
        font-weight: bold;
      }
    }
    position: -webkit-sticky;
    position: sticky;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    color: $color;
    left: 14.583vw;
    right: 30%;
    width: 80%;
    height: 100vh;
    padding: 20px;
    line-height: 3.472vw;
  }
  
}
