@import "_config.scss";
$color: #001f3f;
/* 
  ##Device = Most of the Smartphones Mobiles (Portrait)
  ##Screen = B/w 320px to 479px
*/
@media screen and (min-width: 320px) and (max-width: $maxWidthPortrait) {
    .experience-new{
        h1 {
            text-align: center;
            font-family: "Quicksand", sans-serif;
            color: $color;
            font-size: 4.083vw;
            margin-bottom: 1.389vw;
            margin-top: 1.389vw;
        }
        .ind-exp-container{
            display: flex;
            flex-direction: row;
            flex-wrap: wrap;
            justify-content: center;
            padding: 20px;
            overflow-y: scroll;
        }
        .wrapper{
            position: relative;
            width: 290px;
            height: 290px;
        }
        .exp1{
          img{
             width: 290px;
             border-radius: 20px;
          }
          .caption{
            text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
            position: absolute;
            top: 120px;
            left: 35px;
            font-size: 20px;
            color: white;
            z-index: 999;
            width: 230px;
            text-align: center;
            font-family: "Poppins", sans-serif;
            background-color: #fa997c;
            padding: 3px;
            border-radius: 10px;
          }
          .caption-long{
            position: absolute;
            display: flex;
            top: 20px;
            left: 25px;
            .text{
              a{
                color: white;
                text-decoration: underline;
              }
              font-size: 12px;
              color: white;
              z-index: 999;
              text-align: left;
              vertical-align: middle;
              font-family: "Lato", sans-serif;
              ul,li{
                padding: 3px;
                margin-left: 10px;
              }
              em{
                font-weight: 700;
                font-style: normal;
              }
            }
            width: 250px;
            height: 250px;
            background-color: #f78765f1;
            padding: 9px;
            border-radius: 10px;
        }
          .time{
            font-size: 20px;
            font-family: "Lato", sans-serif;
            color: $color;
            text-align: right;
            margin-bottom: 20px;
          }
        //   margin-right: 20px;
        //   border: 5px solid red;
        }
        .exp-container{
          width: 90%;
          height: 81vh;
          background-color: #f3feff;
          border-radius: 1.042vw;
          display: flex;
          padding: 20px;
          flex-direction: column;
        }
        position: -webkit-sticky;
        position: sticky;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
        color: $color;
        left: 14.583vw;
        right: 30%;
        width: 100%;
        height: 100vh;
    }
}

